/* ============= Theme Mixins ============= */
/* ============= Base ============= */
/*********** Theme Generic **********/
.d-light i.fa,
.d-dark i.fa {
  vertical-align: middle;
}

.d-dark {
  display: none !important;
}

html,
html *,
html *:before,
html *:after {
  scrollbar-color: rgba(0, 0, 0, 0.2) #f5f5f5;
  scrollbar-width: thin;
}
html::-webkit-scrollbar,
html *::-webkit-scrollbar,
html *:before::-webkit-scrollbar,
html *:after::-webkit-scrollbar {
  width: 6px;
  background-color: #f5f5f5;
}
html::-webkit-scrollbar-thumb,
html *::-webkit-scrollbar-thumb,
html *:before::-webkit-scrollbar-thumb,
html *:after::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.125);
  border-radius: 0.5rem;
  -webkit-border-radius: 0.5rem;
  -moz-border-radius: 0.5rem;
}

body {
  font-family: "Inter", sans-serif;
  font-feature-settings: "liga" 1, "calt" 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #626262;
  padding-top: 65px;
}

@supports (font-variation-settings: normal) {
  body {
    font-family: "InterVariable", sans-serif;
  }
}
h1, .h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-family: "Metropolis", sans-serif;
  color: #222222;
  font-weight: 700;
}

code {
  background: #f5f5f5;
  color: #222222;
  padding: 0.25rem 0.375rem;
  border-radius: 0.25rem;
  -webkit-border-radius: 0.25rem;
  -moz-border-radius: 0.25rem;
}

a.theme-link {
  color: #222222;
  text-decoration: underline;
  -webkit-text-decoration-color: rgba(34, 34, 34, 0.3);
  text-decoration-color: rgba(34, 34, 34, 0.3);
}
a.theme-link:hover {
  color: #007bff;
  -webkit-text-decoration-color: rgba(0, 123, 255, 0.8);
  text-decoration-color: rgba(0, 123, 255, 0.8);
}

a.text-link {
  text-decoration: none;
}
a.text-link:hover {
  text-decoration: underline;
}

.theme-icon-holder {
  display: inline-block;
  background: #f5f5f5;
  color: #007bff;
  width: 32px;
  height: 32px;
  padding-top: 4px;
  font-size: 1rem;
  text-align: center;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
}

.theme-bg-light {
  background: #f5f5f5;
}

.theme-bg-dark {
  background: #007bff;
}

.theme-bg-shapes-right {
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0;
  top: 0;
  overflow: hidden;
}
.theme-bg-shapes-right:before {
  content: "";
  width: 300px;
  height: 150px;
  background: rgba(255, 255, 255, 0.1);
  position: absolute;
  right: -60px;
  top: 0;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  transform: skew(25deg, -10deg);
  moz-transform-origin: top left;
  transform-origin: top left;
}
.theme-bg-shapes-right:after {
  content: "";
  width: 150px;
  height: 600px;
  background: rgba(255, 255, 255, 0.15);
  position: absolute;
  right: 0px;
  top: 30px;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  transform: skew(25deg, 10deg);
  moz-transform-origin: top left;
  transform-origin: top left;
}

.theme-bg-shapes-left {
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0;
  top: 0;
  overflow: hidden;
}
.theme-bg-shapes-left:before {
  content: "";
  width: 300px;
  height: 150px;
  background: rgba(255, 255, 255, 0.1);
  position: absolute;
  left: -90px;
  top: 0;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  transform: skew(25deg, -12deg);
  moz-transform-origin: top left;
  transform-origin: top left;
}
.theme-bg-shapes-left:after {
  content: "";
  width: 150px;
  height: 600px;
  background: rgba(255, 255, 255, 0.15);
  position: absolute;
  left: 0px;
  top: 30px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  transform: skew(-25deg, 10deg);
  moz-transform-origin: top left;
  transform-origin: top left;
}

.break {
  flex-basis: 100%;
  height: 0;
}

.break-column {
  flex-basis: 100%;
  width: 0;
}

